/* ......................... */
/* Footer */
/* ......................... */

.footer-section {
  background: #151414;
  position: relative;
}
.footer-container{
  margin: 10px 100px;
}
.footer-cta {
  border-bottom: 1px solid #373636;
  padding: 20px 0px;
}

.footer-content {
  position: relative;
  z-index: 2;
  padding: 20px 0px;
}
.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%;
}
.footer-logo {
  margin-bottom: 30px;
}
.footer-logo img {
    max-width: 200px;
}
.footer-text p {
  margin-bottom: 14px;
  font-size: 20px;
  color: #7e7e7e;
  line-height: 28px;
}


.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background:  var(--theme-blue);
}
.footer-widget ul li {
  display: inline-block;
  float: left;
  width: 50%;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover{
  color:  var(--theme-blue);
}
.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
}

.copyright-area{
  background: #202020;
  padding: 20px 100px;
  
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a{
  color:  var(--theme-blue);
}
.footer-menu {
    text-align: right;
  
  }
.footer-menu li {
  display: inline-block;
  margin-left: 20px;
  text-align: right;

}
.footer-menu li:hover a{
  color:  var(--theme-blue);
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}
