
.image-carousel-container{
    /* border: 1px solid black; */
    /* width: 100vw; */
    margin-bottom: 30px;

}

.image-carousel {
    text-align: center;


  }
  .image-carousel #imgList {
      width: 90%;
      scroll-behavior: smooth;
      display: inline-block;
      position: relative;
      vertical-align: middle;
      text-align: center;
      overflow-x: scroll;
      overflow-y: hidden;
      white-space: nowrap;
      -webkit-overflow-scrolling: touch;
      margin: auto;
    }
    .image-carousel #imgList::-webkit-scrollbar {
      display: none;
    }
.imgList-item {
      list-style: none;
      display: inline-block;
      position: relative;
      vertical-align:top;
      /* width: 20%; */
      margin-left: 10px;
      margin-right: 10px;
      /* filter: grayscale(0); */
        transition: 0.2s;
        width: 500px;

    }

  .imgList-item img{
    width: 100%;
    border-radius: 10px;
  }
  .imgList-item:hover{
    transition: 0.2s;
    /* filter: grayscale(100%); */
  }
    .imgList-item:hover .image-item-caption{
      transition: 0.2s;
      display: block;
      margin-top: -50px;
     

    }


    .image-item-caption{
      display: none;
      width: 100%;
      background-size: cover;
      padding: 5px;
      color: white;
    }
    .image-item-caption p{
      font-size: 20px;
    }

.image-carousel-btn {
  /* position: absolute; */
  width: 40px;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 50%;
  /* z-index: 100; */
  cursor: pointer;
  background-color: rgba(202, 202, 202, 0.7);
  font-size: 18px;
}
.image-carousel-btn:active {
  transform: scale(1.1);
}
.btn-prev {
  top: 95vh;
  left: 2%;
}

.btn-next {
  top:95vh;
  right: 2%;
}







@media (max-width: 767px){

.image-carousel-container{
  margin-bottom: 20px;

}

.image-carousel #imgList {
    width: 80%;
  }
.imgList-item {
    margin-left: 10px;
    margin-right: 10px;
      width: 300px;

  }

.imgList-item img{
  width: 100%;
  border-radius: 10px;
}
  .imgList-item:hover .image-item-caption{
    margin-top: -50px;
   

  }


  .image-item-caption{
    display: none;
    width: 100%;
    background-size: cover;
    padding: 5px;
    color: white;
  }
  .image-item-caption p{
    font-size: 16px;
  }

.image-carousel-btn {
width: 30px;
height: 30px;
padding: 5px;
font-size: 16px;
}






}