.news-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  margin: auto;
  width: 100%;
  height: 100%;
  font-size: 12px;
}

.news-table td, th {
  border: 1px solid #ddd;
  padding: 5px 0px;

}

.news-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* .news-table tr:hover {background-color: #ddd;} */

.news-table th {
  padding: 8px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #d9d9d9;
  color: rgb(0, 0, 0);
  font-size: 12px;
}


