:root {
    --theme-blue: #00AEEF;
    --theme-light-grey: rgb(245, 245, 245);
}
a{
    text-decoration: none;
}

.navbar {
    background: var(--theme-light-grey);
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    z-index: 100;
    justify-content: space-between;
    top: 0;
    position: sticky;
    /* box-shadow:  5px 0px 10px 0px rgb(85, 84, 84); */
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);

}

.logo-image img{
    width: 300px;
    /* height: 50px; */
    align-items: center;
    margin: auto;

    /* display: flex; */
    /* margin: 5px; */
}

.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    margin: auto 20px;
    
  }
  

.nav-logo {
    align-items: center;
    margin-left: 20px;
    flex-grow: 1;

}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: space-between;

}

.nav-links {

    color: black;
    text-decoration: none;
    margin: 0 10px;
    padding: 10px 0px;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    align-items: center;
    border: none;
    cursor: pointer;
    
    
}
.nav-links:hover {
    /* background-color: var(--theme-blue); */
    border-bottom: 3px solid var(--theme-blue);
    /* transition: all .5s; */
    text-decoration: none;
    color: black;

}



.active-nav{
    color: black;
    text-decoration: none;
    padding: 10px 0px;
    margin: 0 10px;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    align-items: center;
    /* background-color: var(--theme-blue); */
    border-bottom: 3px solid var(--theme-blue);
    /* transition: all .5s; */

}
.active-nav:hover{
    color: black;
    text-decoration: none;
    

}

.nav-dropbtn{
    display: inline-block;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    border: none;
    background-color: var(--theme-light-grey);
    /* margin: 0 10px; */
    /* padding: 10px 0px; */
    color: black;
    text-decoration: none;
    align-items: center;

  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: var(--theme-light-grey);
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.1);
    z-index: 1000;
    color: black;
    /* margin-top: 3px; */
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 0 0 4px 4px;
    
  }

.drop-nav-links {
    float: none;
    display: block;
    color: black;
    text-decoration: none;
    font-size: 14px;
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    text-align: left;
    padding: 0px 10px;
    margin: 5px;    
  }
  .active-drop{
    float: none;
    display: block;
    color: black;
    text-decoration: none;
    font-size: 14px;
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    text-align: left;
    padding: 0px 10px;
    margin: 5px;
    background-color: rgb(228, 228, 228);


  }
  .active-drop:hover{
    color: black;
    text-decoration: none;

  }
  .drop-nav-links:hover {
    background-color: rgb(228, 228, 228);
    text-decoration: none;
    color: black;
  }



  .nav-item :hover .dropdown-content {
    display: block;
    text-decoration: none;
    
  }




.fa-code {
    margin-left: 1rem;
}

.nav-item {
    line-height: 50px;
    margin-right: 0.5rem;
}


.nav-icon {
    display: none;
}



@media screen and (max-width: 767px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 62px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
        box-shadow:  5px 0px 10px 0px #aaa;
        text-align: left;
        padding: 10px;
        background: var(--theme-light-grey);
    }
    .nav-logo {
        margin-left: 0px;

    
    }
    .logo-image img{
        width: 250px;
    }

    .nav-menu.active {
        background-color: var(--theme-light-grey);
        border-bottom: none;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-item {
        margin-right: 0;
        background: var(--theme-light-grey);
    }

    .nav-links {
        padding:0px 10px;
        width: 100%;
        display: table;
        margin: 0px;
        border-bottom: none;
        background: var(--theme-light-grey);
    }
    .nav-dropbtn{
        display: inline-block;
        font-weight: 500;
        font-family: 'Raleway', sans-serif;
        font-size: 16px;
        border: none;
        /* background-color: var(--theme-light-grey); */
        background: var(--theme-light-grey);
        /* margin: 0 10px; */
        /* padding: 10px 0px; */

    
      }
    .active-nav{
        padding:0px 10px;
        width: 100%;
        display: table;
        margin: 0px;
        border-bottom: none;
    
    }

    .nav-icon {
        display: block;
        position: absolute;
        top: 10;
        right: 20px;
        font-size: 30px;
        cursor: pointer;
        align-items: center;
        background-color:var(--theme-blue);
        padding:5px 10px ;
        border-radius: 5px;
    }
    .active-drop{
        float: none;
        display: block;
        color: black;
        text-decoration: none;
        font-size: 14px;
        font-family: 'Raleway', sans-serif;
        font-weight: 500;
        text-align: left;
        padding: 0px 10px;
        margin: 5px;
        background-color: rgb(228, 228, 228);
       
    
    
      }
}